
import Msg from 'vux/src/components/msg/index.vue'

export default {
name:'logout',
data(){
return{
isShowMsg:false,
buttons:[
{
type:'primary',
text:'',
link:'/login'
}
]
}
},
components:{
Msg
},

created:function(){

if(this.$store.state.isLogined==false){
this.isShowMsg = true;
return;
}


this.$store.commit('mutationLogout');

window.localStorage.clear();

this.$router.push('/');
}
}



