//vuex
import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations.js';
import actions from './actions.js';

// 
Vue.use(Vuex);
const store = new Vuex.Store({
    //state

    // this.$store.state. A
    // mapState ... B
    state:{
        topics:[
            {name:'all',desc:'',selected:true},
            {name:'good',desc:'',selected:false},
            {name:'share',desc:'',selected:false},
            {name:'ask',desc:'',selected:false},
            {name:'job',desc:'',selected:false},
            {name:'dev',desc:'',selected:false},
          ],
          
          // 
            unloginedMenus:{
            login:'',
            // menu2:'',
            // menu3:''
            },
          // 
          loginedMenus:{
            create:'',
            msg:'',
            center:'',
            logout:'',
          },
            
          isLogined:false,
          isShowMoreMenus:false,
          loginname:'',
          // accesstoken
          accesstoken:'',
          // mutation
          author_id:'',
    },
    getters:{
        moreMenus:function(state){
            // state
            if(state.isLogined){
            // thisstate
            // 
            return state.loginedMenus;
            } 
            return state.unloginedMenus;
            // return this.unloginedMenus;
          },
          getTopicDescByTab:function(state){
            return function(tab){
              for(var i=0;i<state.topics.length;i++){
                if(state.topics[i].name==tab){
                  return state.topics[i].desc;
                }
              }
            }
          },

    },
    
    //
    mutations,
    //
    actions
})
// 
export default store;


